.sidebar-title
{
    border-left: 4px solid #FF5733;
    padding-left: 10px;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 20px;
}

.sidebar-items
{
    margin-top: 20px;
}

.sidebar-label-container
{
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
}

.sidebar-label-container input
{
    position: absolute;
    opacity: 0;
    cursor: pointer;   
}

.checkmark
{
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #eee;
    border-radius: 50%;
}

.sidebar-label-container:hover input ~ .checkmark
{
    background-color: #ccc;
}

.sidebar-label-container input:checked ~ .checkmark
{
    background-color: #FF5733;
}

